.sidebar-container {
  z-index: 1000;
  background: var(--color-bg-sidebar);
  box-shadow: var(--shadow-lg);
  min-width: 320px;
  transition: all .3s;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
}

.sidebar-show {
  margin-left: 0;
}

.sidebar-hide {
  margin-left: -400px;
}

.sidebar-block {
  z-index: 990;
  opacity: .4;
  background: #000;
  animation: 1s linear to-block;
  position: fixed;
  inset: 0;
}

@keyframes to-block {
  0% {
    opacity: 0;
  }

  100% {
    opacity: .4;
  }
}

.sidebar-header {
  align-items: center;
  padding: .4rem;
  display: flex;
}

.sidebar-header button {
  color: #fff;
  transition: all .3s;
}

.sidebar-close {
  text-shadow: var(--shadow-text);
  background: none;
  border: none;
  font-size: 2rem;
  transition-duration: .15s;
}

.sidebar-close:hover {
  text-shadow: unset;
}

.sidebar-button {
  align-items: center;
  padding: .7rem 1rem;
  font-size: 1.2rem;
  font-weight: 600;
  transition: all .2s;
  display: flex;
}

.sidebar-button-inactive {
  color: var(--color-primary-300);
}

.sidebar-button-inactive:hover {
  border-color: var(--color-primary-300);
  background: var(--color-primary-300);
  color: #fff;
}

.sidebar-button-active {
  color: var(--color-primary-300);
  background: var(--color-primary-300);
}

